.aside {
  position: fixed;
  z-index: 99999;
  left: 0;
  right: 0;
  top: 0;
  padding: 14px;
  display: flex;
  justify-content: flex-end;
  background-color: #fff;
  &--active {
    transform: translateY(-100%);
  }
  &__sign {
    width: 120px;
    height: 22px;
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
  }
  @include media-breakpoint-up(lg) {
    left: 0;
    float: left;
    width: 6vw;
    height: 100vh;
    justify-content: center;
    &__sign {
      transform: translate(-50%, -50%) rotate(-0.25turn);
    }
    &--active {
      transform: translateX(-100%);
    }
  }
}
// NAV
.nav {
  position: relative;
  z-index: 9999;
  width: 18px;
  height: 14px;
  span {
    position: absolute;
    width: 100%;
    height: 2px;
    background-color: $secondary;
    transition: 0.3s $easeOutQuart;
    &:nth-child(1) {
      top: 0;
    }
    &:nth-child(2) {
      top: 6px;
    }
    &:nth-child(3) {
      bottom: 0;
    }
  }
  &--active {
    transform: translateY(300%);
    span {
      background-color: $white;
      &:nth-child(1) {
        top: 6px;
        transform: rotate(45deg);
      }
      &:nth-child(2) {
        opacity: 0;
      }
      &:nth-child(3) {
        bottom: 6px;
        transform: rotate(-45deg);
      }
    }
    @include media-breakpoint-up(lg) {
      transform: translateX(300%);
    }
  }
}
//MENU
.menu {
  z-index: 999;
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  width: 100vw;
  height: 100vh;
  background-color: $primary;
  padding: 60px;
  transform: translateX(-100%);
  transition: 0.3s $easeOutQuart;
  &--active {
    transform: translateX(0);
  }
}
//content
.content {
  transform: translateX(0);
  @include media-breakpoint-up(lg) {
    width: 100vw;
    height: 100vh;
    overflow-x: hidden;
    overflow-y: hidden;
    padding-left: 6vw;
    & > section {
      width: calc(94vw * 3);
    }
  }
}
.workContent {
  padding-top: 6vh;
  @include media-breakpoint-up(lg) {
    width: 100vw;
    height: 100vh;
    overflow-x: hidden;
    float: right;
  }
}
