@import url("https://fonts.googleapis.com/css2?family=Source+Serif+Pro:wght@600&display=swap");
@import url("https://fonts.googleapis.com/css2?family=Noto+Sans+TC:wght@100;300;500;700&family=Source+Serif+Pro:wght@600&display=swap");
.textEng {
  font-family: "Source Serif Pro", serif;
}
.text-underline {
  position: relative;
  &:after {
    content: "";
    display: block;
    position: absolute;
    width: 100%;
    height: 2px;
    bottom: -1px;
    left: 0;
  }
  &-primary {
    &:after {
      background-color: $primary;
    }
  }
  &-success {
    &:after {
      background-color: $success;
    }
  }
}
