.btn-border {
  display: block;
  padding-left: $spacer * 0.5;
  border-left: 2px solid $primary;
  transition: 0.2s $easeInOutCirc;
  &:hover {
    letter-spacing: 0.02rem;
    color: $light !important;
  }
}

.workList {
  position: relative;
  max-height: 95vh;
  width: 100%;
  @include media-breakpoint-up(lg) {
    width: 80vw;
    right: -5vw;
    height: 95vh;
  }
  &__item {
    width: 40vw;
    height: 52vw;
    margin-bottom: 3vw;
    display: block;
    border-radius: 4px;
    box-shadow: 0 0 10px rgba(#aeaeae, 0.4);
    background-position: center;
    background-repeat: no-repeat;
    background-size: cover;
    transition: 0.3s $easeOutQuart;
    @include media-breakpoint-up(md) {
      width: 30vw;
      height: 38vw;
      margin-bottom: 2vw;
    }
    @include media-breakpoint-up(lg) {
      width: 16vw;
      height: 21vw;
      margin-right: 25px;
      margin-bottom: 0;
    }
    &:hover {
      transform: translateY(-8px);
      box-shadow: 0 8px 7px rgba(#aeaeae, 0.3);
    }
  }
}

.workImg {
  width: 95%;
  @include media-breakpoint-up(lg) {
    width: 75%;
  }
  ul {
    width: 100%;
  }
  img {
    display: block;
    width: 100%;
  }
}

.icon {
  &--arrow {
    transition: all 0.45s cubic-bezier(0.65, 0, 0.076, 1);
    margin-left: 5px;
    position: relative;
    display: block;
    width: 10px;
    height: 1px;
    background-color: none;
    &::after {
      transition: all 0.45s cubic-bezier(0.65, 0, 0.076, 1);
      content: "";
      display: block;
      position: relative;
      top: -0.25rem;
      width: 8px;
      height: 8px;
      transform: rotate(45deg);
      border: 1px solid $primary;
      border-bottom: 0 solid;
      border-left: 0 solid;
      left: -2px;
    }
  }
}
.landingBtn {
  position: relative;
  padding: 10px 20px;
  border: 1px solid $light;
  display: flex;
  background-color: none;
  justify-content: center;
  align-items: center;
  margin: 0 auto;
  &:hover {
    border: 1px solid $primary;
    background-color: $primary;
    p {
      color: $white !important;
    }
    .icon--arrow {
      background-color: $white;
      &::after {
        border: 1px solid $white;
        border-bottom: 0 solid;
        border-left: 0 solid;
        left: 0px;
      }
    }
  }
}
