.indexBox {
  width: auto;
  height: auto;
  @include media-breakpoint-up(lg) {
    width: 94vw;
    height: 100vh;
  }
}
.hero {
  &__img {
    display: block;
    width: 100%;
    height: 400px;
    background-color: pink;
    background-size: cover;
    background-repeat: no-repeat;
    background-image: url("../images/hero_img.png");
    background-position: center;
    @include media-breakpoint-up(sm) {
      background-position: top;
    }
  }
  &__content {
    margin-top: 30px;
  }
  @include media-breakpoint-up(lg) {
    &__img {
      width: 500px;
      height: 100%;
    }
    &__content {
      display: flex;
      justify-content: center;
      align-items: flex-start;
      flex-direction: column;
    }
  }
}
//
.about {
  li {
    @include media-breakpoint-up(lg) {
      width: 28%;
    }
  }
}
//
.carousell {
  @include media-breakpoint-up(lg) {
    position: absolute;
    right: -7vw;
    bottom: 80px;
  }
}
.swiper {
  width: 100vw;
  height: 30vw;
  @include media-breakpoint-up(lg) {
    width: 75vw;
    height: 20vw;
  }
}

.swiper-slide {
  a {
    display: block;
    width: 100%;
    height: 100%;
    background-color: pink;
    background-position: center;
    background-repeat: no-repeat;
    background-size: cover;
  }
}

.swiper-slide img {
  display: block;
  width: 100%;
  height: 100%;
  object-fit: cover;
}
